@media (max-width: 768px) {
  .landing {
    padding: 2rem 1rem;
    justify-content: center;
  }

  .hero-layout {
    flex-direction: column;
    gap: 2rem;
    text-align: center;
  }

  .avatar {
    width: 140px;
    height: 140px;
  }

  .intro-block {
    text-align: center;
  }

  .tagline {
    flex-direction: column;
    font-size: 1rem;
    transform: none;
    max-width: 100%;
  }

  .typed-heading {
    font-size: clamp(2rem, 6vw, 3rem);
  }
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.landing {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: var(--color-bg);
  color: var(--color-navy);
  padding: 4rem 2rem;
}

.hero-layout {
  display: flex;
  align-items: center;
  gap: 3rem;
  max-width: 960px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.avatar {
  flex-shrink: 0;
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.1);
}

.intro-block {
  flex: 1;
  text-align: left;
}

.intro-block h1 {
  font-family: var(--font-heading);
  font-size: clamp(2.5rem, 6vw, 4rem);
  font-weight: 700;
  margin-bottom: 0.5rem;
  line-height: 1.2;
}

.intro-block h2 {
  font-family: var(--font-body);
  font-size: clamp(1.25rem, 3vw, 2rem);
  font-weight: 600;
  color: var(--color-accent);
  margin-bottom: 1rem;
}

.tagline {
  display: flex;
  align-items: baseline;
  font-size: 1.1rem;
  font-weight: 400;
  color: var(--color-text);
  line-height: 1.6;
  max-width: 600px;
  transform: translateX(5px);
}

.tagline-prefix {
  margin-right: 0.34rem;
  font-weight: 500;
  animation-delay: 0.5s;
  border-bottom: 2px solid var(--color-accent);
  padding-bottom: 2px;
  animation: fadeIn 3s ease forwards;
  animation: fadeInUp 2s ease forwards;
}

.typed-heading {
  font-family: var(--font-heading);
  font-size: clamp(2.5rem, 6vw, 4rem);
  font-weight: 700;
  margin-bottom: 0.5rem;
  line-height: 1.2;
}

.typed-wrapper {
  display: inline-block;
  vertical-align: baseline;
  animation-delay: 1s;
  animation: fadeIn 1s ease forwards;
  animation: fadeInUp 1s ease forwards;
}

.tagline-prefix,
.typed-wrapper { 
  opacity: 0;
}

.typed-wrapper .typed-cursor {
  opacity: 0;
  animation: showCursor 0.3s ease forwards;
  animation-delay: 1.5s; /* match startDelay */
}

@keyframes showCursor {
  to {
    opacity: 1;
  }
}

/* Glitch Effect */

.glitch-text {
  position: relative;
  display: inline-block;
  animation: glitch 1.2s infinite;
}

@keyframes glitch {
  0% {
    text-shadow: 2px 0 red, -2px 0 blue;
  }
  20% {
    text-shadow: -2px 0 red, 2px 0 blue;
  }
  40% {
    text-shadow: 2px 0 red, -2px 0 blue;
  }
  60% {
    text-shadow: -2px 0 red, 2px 0 blue;
  }
  80% {
    text-shadow: 2px 0 red, -2px 0 blue;
  }
  100% {
    text-shadow: none;
  }
}

.tagline-prefix,
.typed-wrapper {
  will-change: transform, opacity;
}
