/* MainContent.css */

/* 1. Desktop: matches your current perfect layout */
.main-content {
  /* padding-top: 7.5rem;   ← your desktop header height */
}

/* 2. Tablet: if header shrinks or menu height changes */
@media (max-width: 1024px) {
  .main-content {
    padding-top: 2rem;  /* adjust to your tablet header height */
  }
}

/* 3. Mobile: even smaller header (e.g., burger menu) */
@media (max-width: 768px) {
  .main-content {
    padding-top: 1rem;  /* adjust to your mobile header height */
  }
}
