@media (max-width: 768px) {
  body {
    font-size: 0.95rem;
  }
}

:root {
  --font-heading: 'Quicksand', sans-serif;
  --font-body: 'Nunito', sans-serif;
  --color-bg: #f5f3ef;
  --color-text: #2e2e2e;
  --color-accent: #c2a85d;
  --color-navy: #1a2a40;
  --color-gold: #C49A3D; /* Warm, muted gold */
  --color-gold-hover: #B5892F;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  background-color: var(--color-bg);
  color: var(--color-text);
}

.app-wrapper {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.main-content {
  flex: 1;
  display: flex;
  justify-content: center; 
  align-items: center;  
}




