@media (max-width: 768px) {
  .about-layout {
    flex-direction: column;
    gap: 2rem;
    padding: 2rem 1rem;
    height: auto;
    overflow: visible;
  }

  .bio-panel {
    padding-right: 0;
    max-width: 100%;
    text-align: center;
  }

  .bio-panel h2 {
    font-size: 1.25rem;
    margin-bottom: 0.75rem;
    padding-bottom: 0.4rem;
  }

  .timeline-panel {
    padding-left: 0;
    max-height: none;
    overflow-y: visible;
  }

  .timeline-panel .chrono-card {
    padding: 1rem 1.25rem;
  }

  .timeline-panel .chrono-card-title,
  .timeline-panel .chrono-card-subtitle {
    font-size: 0.9rem;
  }

  .timeline-panel .chrono-card-text {
    font-size: 0.75rem;
    line-height: 1.6;
  }
}

.about-layout {
  display: flex;
  gap: 3rem;
  padding: 4rem 2rem;
  background-color: var(--color-bg);
  color: var(--color-text);
  height: calc(100vh - 7.5rem);
  overflow: hidden;
}

.about-link {
  cursor: pointer;
  color: var(--color-navy);
  transition: color 0.3s ease;
}

.about-link:hover {
  color: var(--color-gold);
}

.bio-panel {
  flex: 1;
  padding-right: 2rem;
  max-width: 520px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.bio-panel h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  border-bottom: 2px solid var(--color-accent);
  padding-bottom: 0.5rem;
}

/* Bio Text Fade */
.bio-text {
  font-size: 0.9rem;
  line-height: 1.8;
  letter-spacing: 0.01em;
  font-weight: 400;
  color: var(--color-text);
}

.fade-in {
  opacity: 0;
  transform: translateY(8px);
  animation: fadeInUp 0.6s ease forwards;
  animation-delay: 0.1s;
}

.fade-out {
  opacity: 0.3;
}

@keyframes fadeInUp {
  from { opacity: 0; transform: translateY(8px); }
  to { opacity: 1; transform: translateY(0); }
}

/* Timeline Panel */
.timeline-panel {
  flex: 1;
  overflow-y: auto;
  max-height: calc(100vh - 7.5rem);
  padding-left: 1rem;
  scroll-behavior: smooth;
}

/* scrollbar styles, which is none */
.timeline-panel *::-webkit-scrollbar {
  width: 0px;
  height: 0px;
  display: none !important; 
}

.timeline-panel * {
  -ms-overflow-style: none !important;
  scrollbar-width: none !important;
}

.timeline-panel .chrono-card-title {
  text-align: center;
  font-weight: 600;
  font-size: 0.9rem;
  color: var(--color-accent);
  margin-bottom: 0.75rem;
  letter-spacing: 0.02em;
}

/* Card Container */
.timeline-panel .chrono-card {
  border-radius: 14px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.06);
  padding: 1.5rem 1.75rem;
  background-color: var(--color-light);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.timeline-panel .chrono-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 28px rgba(0, 0, 0, 0.08);
}

/* Subtitle (Role) */
.timeline-panel .chrono-card-subtitle {
  font-size: 1rem;
  font-weight: 600;
  color: var(--color-gold);
  margin-bottom: 0.5rem;
  text-align: center;
}

.timeline-panel .chrono-card-text {
  font-size: 0.8rem;
  line-height: 1.7;
  color: var(--color-text);
  text-align: justify;
  opacity: 0;
  transform: translateY(10px);
  transition: all 0.6s ease;
}

.timeline-panel .chrono-card:hover .chrono-card-text,
.timeline-panel .chrono-card.in-view .chrono-card-text {
  opacity: 1;
  transform: translateY(0);
}

.timeline-panel .chrono-card h3,
.timeline-panel .chrono-card h4 {
  margin: 0.25rem 0;
}

.timeline-panel .chrono-card p {
  margin-top: 0.5rem;
}

.timeline-panel .active {
  color: var(--color-navy) !important;
}

/* Hide Timeline Controls */
.timeline-panel [role="toolbar"] {
  display: none;
}
