/* Desktop only: six columns */
@media (min-width: 1025px) {
  .skills-grid {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 0.5rem;
    justify-items: center;
  }
}

/* Tablet only (two columns) */
@media (max-width: 1024px) and (min-width: 769px) {
  .skills-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }

  /* …other tablet tweaks… */
}

/* Mobile only (two columns, multiple rows) */
@media (max-width: 768px) {
  .skills-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
  }

  .skill-card {
    min-height: 100px;
    padding: 0.75rem;
    /* remove scroll, flex, and carousel styles */
  }
  
  .skill-logo {
    height: 48px;
    max-width: 64px;
    margin-bottom: 0.5rem;
  }
  
  .skill-category {
    font-size: 0.75rem;
  }
}

.skills-section {
  text-align: center;
  padding: 8rem 4rem 4rem; /* Top padding increased to push below HeaderNav */
  background-color: var(--color-bg);
  overflow-x: hidden; /* Prevent horizontal scroll */
  max-width: 1200px;
  margin: 0 auto; /* Center the section */
}

.skills-heading {
  font-family: var(--font-heading);
  font-size: 2rem;
  color: var(--color-navy);
  margin-bottom: 0.5rem;
}

.skills-subtext {
  font-size: 1rem;
  color: var(--color-text);
  margin-bottom: 2rem;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

.skills-grid {
  display: grid;
  grid-template-columns: repeat(6, 1fr); /* Force 6 columns */
  gap: 0.5rem;
  justify-items: center;
  width: 100%;
  max-width: 100%; /* Ensure full container width */
}

.skill-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: transparent;
  border-radius: 12px;
  padding: 1rem;
  transition: transform 0.3s ease;
  min-height: 120px; /* Ensures vertical breathing room */
  opacity: 0;
  transform: translateY(10px);
  animation: fadeInUp 0.6s ease forwards;
  animation-delay: calc(0.05s * var(--i));
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.skill-card:hover {
  transform: translateY(-5px);
}

.skill-logo {
  height: 64px; /* Make the logo the star */
  max-width: 80px;
  object-fit: contain;
  margin-bottom: 0.75rem;
}

.skill-name {
  font-weight: 600;
  font-size: 0.9rem;
  color: var(--color-navy);
}

.skill-category {
  font-size: 0.8rem;
  color: var(--color-accent);
  font-weight: 500;
  text-align: center;
}
