@media (max-width: 768px) {
  .social-links {
    flex-direction: column;
    gap: 0.75rem;
  }

  .site-footer {
    font-size: 0.7rem;
    padding: 1rem 0.5rem;
  }

  .attribution {
    font-size: 0.65rem;
  }
}

.site-footer {
  background-color: var(--color-bg);
  padding: 1rem;
  text-align: center;
  font-size: 0.75rem;
  color: var(--color-text);
  border-top: 1px solid #ddd;
}

.social-links {
  margin: 1rem 0;
  display: flex;
  justify-content: center;
  gap: 1.5rem;
}

.social-links a {
  color: var(--color-navy);
  text-decoration: none;
  font-weight: 600;
  transition: color 0.3s ease;
}

.social-links a:hover {
  color: var(--color-accent);
}

.attribution {
  font-size: 0.75rem;
  margin-top: 1rem;
  color: #888;
}

